<template>
  <div class="d-none d-sm-block mx-1 h-100 position-fixed leftPanel z-3">
    <div>
      <!-- <div class="my-1">
        <img class="calculator" width="50" height="50" :src="calculator.image" @click="calculatorModal" title="Калькулятор" />
      </div> -->
      <div>
        <img class="purchase" width="55" height="50" :src="logo" @click="orders" title="Закупівлі" />
      </div>
    </div>
  </div>

  <div v-if="calculator.modal" class="modal d-block" tabindex="-1">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h6 class="modal-title">Калькулятор вирахування платежу за послугу на 1м2</h6>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="calculator.modal = false"></button>
        </div>
        <div class="modal-body">
          <p class="m-0">Сервіс у розробці ...</p>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="calculator.modal = false">Закрити</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import logo from '@/assets/logo_3.png';
import calculator from '@/assets/calculator.png';

export default {
  data() {
    return {
      logo: logo,
      calculator: {
        image: calculator,
        modal: false
      }
    };
  },
  methods: {
    ordersModal() {
      const body = document.body;
      const modal = document.querySelector('#modal_prom_state_purchase_block');
      if(modal.style.display === 'block') {
        modal.style.display = 'none';
        body.style.overflow = "auto";
      } else {
        modal.style.display = 'block';
        body.style.overflow = "hidden";
      }
    },

    orders() {
      this.calculator.modal = false;
      this.ordersModal();
    },

    calculatorModal() {
      const body = document.body;
      const modal = document.querySelector('#modal_prom_state_purchase_block');
      modal.style.display = 'none';
      body.style.overflow = "auto";

      this.calculator.modal = true;
    }
  },
}
</script>
