  body {
    background: #F7F7F7;
  }

  a {
    cursor: pointer;
  }

  .hover {
    font-weight: bold;
    background: white;
    color: black;
  }

  table.dataTable {
    border-collaps: collaps;
  }

  .paging_simple_numbers {
    margin-top: 15px;
  }

  .ck-editor__main .ck-editor__editable {
    height: 300px;
  }
</style>